import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import test from '@/components/test'
import testChildren from '@/components/testChildren'
import testChildren2 from '@/components/testChildren2'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      alias: '/b',
      component: HelloWorld
    },
    {
      path:'/test/:id',
      component:test,
      // beforeEnter:(to, from, next)=>{
      //   // console.log(to.name,from,next);
      //   next()
      // },
      children:[
        {
          path:'posts',
          component:testChildren
        },
        {
          path:'',
          component:testChildren2
        }
      ]
    },

  ]
})



