<template>
    <div>
        <p>参数:{{id}}</p>
        <router-link :key="111" v-if="id!=111" :to="{path:'/test/111'}">111</router-link>
        <router-link :key="222" v-if="id!=222" :to="{path:'/test/222'}">222</router-link>
        <router-link :key="333" v-if="id!=333" :to="{path:'/test/333'}">333</router-link>
        <p v-for="it in arr">{{it}}</p>
        <button @click="arr=[]">清空数组</button>
        <p>a{{arrStr}}</p>
        <p>b{{arrStr}}</p>
    </div>
</template>
<script>
export default {
    data(){
        return{
            id:this.$route.params.id,
            arr:[1,2,3,4,5]
        }
    },
    computed:{
        arrStr(){
            console.log('arrStr')
            return this.arr.join('/')
        }
            
    },
    watch: {
        '$route' (to, from) {
            console.log('watch')
        // 对路由变化作出响应...
        }
    },
    beforeUpdate(){
        console.log('beforeUpdate')
    },
    updated(){
        console.log('updated')
    },
    mounted(){
        console.log('我是mounted,我的id是',this.id)
    },
    beforeRouteEnter(to,from,next){
        console.log('beforeRouteEnter');
        next()
    },
    beforeRouteUpdate (to,from,next){
        console.log(this.$route.params.id)
        console.log('beforeRouteUpdate ',to.path,from.path)
        next();
    },
    beforeRouteLeave(to,from,next){
        console.log('beforeRouteLeave')
        next();
    }
}
</script>




